import '@/common/con_list.css';
export default {
	data() {
		return {
			itemlist:[],
		};
	},
	onLoad(options) {
		console.log(options.tagkey)
		this.list(options.tagkey)
	},
	methods:{
		list(tagkey){
			let self=this
			uni.request({
				url: 'https://api-h5.soulapp.cn/html/v2/tag/post?tagIdEcpt='+tagkey,
				header: {     
				      'X-Requested-With': 'XMLHttpRequest',
				      'Content-Type': 'application/x-www-form-urlencoded'
				     },
				// method:'POST',
				
				success: function(res) {
					console.log(res.data.data.posts)  
					self.itemlist=res.data.data.posts;
				}
			});
		},
		tags(tagkey){
			uni.navigateTo({
				url: '/pages/soul/tags?tagkey='+tagkey,
			})
		},
	}
}
